import AnimatedPage from '../components/Animations/AnimatedPage';

function About() {
    // const params = useParams();
    // console.log(params)
    return(
            <AnimatedPage>
                <div style={{ flex: 1, margin: "50px 50px 50px", backgroundColor:"red"}}>
                    <h1 style={{ marginBottom: "50px" }}>About Me</h1> 
                    <div style={{ display: "flex" }}>
                        <div>
                            <p>
                                I am a student at Georgia Gwinnett College, pursuing a degree in Information Technology 
                                with a concentration in Software Development.  Since I was kid, I was very creative and analytical.
                                Initially, I wanted to become an Achitectural Engineer because it provided me an opportunity to
                                utilize my skills and build something tangible.  When I took my first programming course, I found
                                that this is my passion and that achitecture was not very fullfuling for me.  Programming allows me
                                to express myself creatively and be involved in the technicalities and intricasies of the 
                                growing digital world.
                            </p>
                            <p>
                                While in college, I have been involved in many team projects 
                                (research and non-research based), and worked as a virtual peer mentor, where I assist freshmen and 
                                sophomore students in professional development and programming skills.  
                                With both my projects and my position, I gained many valuable soft skills (e.g. communication and 
                                teamwork) and hard skills (e.g. coding in Java and JavaScript). 
                                
                                My goal post-graduation is to become a full-stack developer where I can work with a team to provide
                                a service to others and use my technical skills.
                            </p>
                        </div>
                        <p>PUT IMAGE HERE</p>
                    </div>
                </div>
            </AnimatedPage>
        // <p>{Number.parseInt(params.id)}</p>
    )
}

export default About;