import { CDBIcon, CDBBtn } from 'cdbreact';

function HomeInfo() {
    return(
        <div style={{ display:"block", margin:"40vh 0 0 15vw" }}>
            <p style={{ fontSize: "80px", letterSpacing: "15px", fontWeight: 300 }}>Khamilah Enjoli Nixon</p>
            <div style={{ display: "flex", justifyContent:"space-between", padding:"25px 0" }}>
                <div>
                    <CDBIcon fab icon="linkedin" size="3x" style={{ paddingRight:"25px" }}/>
                    <CDBIcon fab icon="github" size="3x"/>
                </div>
                <p style={{ fontSize: "36px", fontWeight: 400, letterSpacing: "4px" }}>Software Engineer</p>
            </div>
            <CDBBtn color="info" size="xl" download="" style={{ fontSize:"24px", width:"200px", height:"60px", border: "none" }}>Download CV</CDBBtn>
        </div>
    )
}

export default HomeInfo;