import React from 'react';
import {
  CDBSidebar,
  CDBSidebarContent,
  CDBSidebarFooter,
  CDBSidebarHeader,
  CDBSidebarMenu,
  CDBSidebarMenuItem,
} from 'cdbreact';

import { NavLink } from 'react-router-dom';

const Sidebar = () => {
  return (
    <div style={{ backgroundColor:"red",height: '100%', overflow: 'scroll initial' }}>
      <CDBSidebar textColor="#333" backgroundColor="#eee">
        <CDBSidebarHeader prefix={<i className="fa fa-bars"/>}>
            My Portfolio
        </CDBSidebarHeader>

        <CDBSidebarContent className="sidebar-content">
          <CDBSidebarMenu>
            <NavLink exact to="" activeClassName="activeClicked">
              <CDBSidebarMenuItem icon="home">Home</CDBSidebarMenuItem>
            </NavLink>
            <NavLink exact to="/skills" activeClassName="activeClicked">
              <CDBSidebarMenuItem icon="hammer">Skills</CDBSidebarMenuItem>
            </NavLink>
            <NavLink exact to="/education" activeClassName="activeClicked">
              <CDBSidebarMenuItem icon="school">Education</CDBSidebarMenuItem>
            </NavLink>
            <NavLink exact to="/experience" activeClassName="activeClicked">
              <CDBSidebarMenuItem icon="laptop">Experience</CDBSidebarMenuItem>
            </NavLink>
            <NavLink exact to="/projects" activeClassName="activeClicked">
              <CDBSidebarMenuItem icon="sticky-note">Projects</CDBSidebarMenuItem>
            </NavLink>
            <NavLink exact to="/accomplishments" activeClassName="activeClicked">
              <CDBSidebarMenuItem icon="trophy">Accomplishments</CDBSidebarMenuItem>
            </NavLink>
            <NavLink exact to="/about" activeClassName="activeClicked">
              <CDBSidebarMenuItem icon="user">About Me</CDBSidebarMenuItem>
            </NavLink>
            <NavLink exact to="/contact" activeClassName="activeClicked">
              <CDBSidebarMenuItem icon="envelope">Contact</CDBSidebarMenuItem>
            </NavLink>
          </CDBSidebarMenu>
        </CDBSidebarContent>

        <CDBSidebarFooter style={{ textAlign: 'center' }}>
          <div
            style={{
              padding: '20px 5px',
            }}
          >
            PUT IMAGE HERE
          </div>
        </CDBSidebarFooter>
      </CDBSidebar>
    </div>
  );
};

export default Sidebar;